.team {
    background: #F5F5FA;
    border-radius:12px  12px  12px 12px;
    margin-top: 30px;


    &__card {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
     
    }

    &__foto {
        text-align: center;
        height: 500px;
        width: 100%;

        &-img {
            border-radius:12px  12px 0 0 ;
            object-fit: cover;
            height: 100%;
            width: 100%;

        }
    }

    &__text {
        padding: 32px ;
      


        &-link {
            margin-left: 46px;

        }

        &-phrase {
            margin-left: 33px;
            margin-top: 4px;
            font-size: 30px;
            width: 81%;
            word-wrap: break-word;
        }

        &-person {
            font-weight: 700;
            line-height: 20px;
            color: #B3B3BA;
            margin-top: 30px;
            margin-left: 36px;
            width: 50%;

        }

        &-position {
        font-size: 14px;
        line-height: 22px;
        color: #B3B3BA;
        margin-top: 5px;
        margin-left: 36px;
        }

    }

}

.team-group {
    text-align: center;
    margin-top: 28px;

    &-btn {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #24A3FF;
    opacity: 0.2;
    border: none;
    margin: 0 5px 0;

    &_active {
    opacity: 1;
    }

    }

}

